
.graph {
    display: flex;
    justify-content: space-between;
    margin: 0 5vh 0 10vh;
  }
  
  .graphHeading {
    color: #000;
    font-size: 18px;
    font-family: Montserrat;
    font-weight: 700;
  }
  
  .date {
    color: #858585;
    font-size: 14px;
    font-family: Montserrat;
  }
  
  .legend {
    display: flex;
    align-items: center;
  }
  
  .red,
  .green {
    height: 1vh;
    width: 1vh;
    border-radius: 100%;
  }
  
  .red {
  
    background-color: rgba(233, 160, 160, 1);
  }
  
  .green {
    background-color: rgba(155, 221, 124, 1);
  }
  
  .user,
  .guest {
    font-size: 12px;
    font-family: Lato;
    margin: 2px;
  }

  .dateImage{
    transform: rotate(90deg);
  }